.App {
    text-align: center;
    background-image: url(../images/city-stars.png);
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    position:relative
  }
  .App::before{
    position: absolute;
      top: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 100vh;
      background: rgba(0, 0, 0, 0.411);
      z-index: -1;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .form-container{
    max-width: 700px;
  }

  .login{
    min-height: 100vh;
  }
  
  
  